/******************************************************************

Stylesheet: BASE

******************************************************************/
html:focus-within {
  scroll-behavior: smooth; }

body {
  font-size: 100%;
  text-rendering: optimizeSpeed;
  margin: 0; }

::selection {
  background: #ff9;
  color: black !important;
  text-shadow: none; }

hr {
  border: 0;
  border-top: 1px solid var(--wp--preset--color--gray-light);
  border-bottom: 1px solid white;
  padding: 0;
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

