/*********************
LINK STYLES
*********************/
a,
a:visited {
  color: var(--link-color); }
  a:hover, a:focus,
  a:visited:hover,
  a:visited:focus {
    color: var(--link-color--hover); }
  a:active,
  a:visited:active {
    color: var(--link-color--active); }

/*********************
LAYOUT & GRID STYLES
*********************/
.wrapper {
  width: 92%;
  width: calc(100% - 40px);
  margin-right: auto;
  margin-left: auto;
  max-width: 1240px; }

.wrapper-inner {
  width: 92%;
  margin-right: auto;
  margin-left: auto;
  max-width: 760px; }

#container {
  position: relative; }

/*********************
GUTENBERG Blocks
similar to old .entryContent
*********************/
/*
.wp-block-post-content.alignfull > :not(.alignfull):not([data-align='full']),
.wp-block[data-align='full']
	> .wp-block-post-content
	> :not(.alignfull):not([data-align='full']),
.wp-site-blocks
	> [class*='wp-container-']
	> :not(.alignfull):not([data-align='full']) {
	margin-left: auto;
	margin-right: auto;
	width: calc( 100% - ( 2 * var( --wp--custom--gutter ) ) ) !important;
}
*/
.wp-site-blocks > *:not(.alignfull) {
  width: calc(100% - 40px);
  max-width: var(--wp--custom--layout-style--content-size);
  margin-right: auto;
  margin-left: auto;
  position: relative; }

.wp-site-blocks > .alignwide {
  max-width: var(--wp--custom--layout-style--wide-size); }

.wp-site-blocks > .alignfull {
  width: 100%;
  max-width: 100%; }

/*********************
ACCESSIBILITY 
using what intent
*********************/
[data-whatintent='mouse'] *:focus {
  outline: none; }

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto; }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important; } }

/*********************
CSS NORMALIZE RESET
*********************/
*,
*::before,
*::after {
  box-sizing: border-box; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
