/*************************************
ARCHIVE PAGE NAVI STYLES
**************************************/
.pagination {
  text-align: center; }
  .pagination ul {
    display: inline-block;
    background-color: white;
    white-space: nowrap;
    padding: 0;
    clear: both; }
  .pagination li {
    padding: 0;
    margin: 0;
    display: inline-block;
    overflow: hidden; }
    .pagination li:last-child {
      border: none; }
  .pagination a,
  .pagination a:visited,
  .pagination span {
    margin: 0;
    text-decoration: none;
    padding: 0;
    line-height: 1em;
    font-size: 1em;
